is permite observar el comportamiento del consumo de energía a través del tiempo en escalas de días, meses, y años. La importancia de este tipo de análisis radica en que se pueden identificar consumos no asociados a la producción como por ejemplo en horarios no laborales, y se pueden medir los valores máximos, mínimos y promedios de los consumos durante el periodo de tiempo que se requiera analizar.")])]),_c('div',{staticClass:"col-md-6 col-lg-6 col-12"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-41.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-7 col-12"},[_c('h4',[_vm._v("Estimación de los consumos energéticos en el futuro:")]),_c('p',[_vm._v("En esta etapa se recurre a las herramientas del análisis gráfico y estadístico que permiten determinar si existe una relación entre algunas de las variables principales de la organización como por ejemplo los niveles de producción (toneladas de producto, hectolitros producidos, cantidad de agua fría, etc.) o los niveles de ocupación para ciertas empresas y organizaciones. ")]),_c('p',[_vm._v("La herramienta más utilizada para tal fin es el análisis de correlación que consiste en obtener una ecuación lineal que represente la relación entre una o más variables que pueden explicar la variación del consumo en función de dichas variables. Una vez obtenida esta ecuación, es posible estimar cuantitativamente los consumos futuros. Existen otras herramientas más avanzadas y que requieren un análisis más detallado.")])]),_c('div',{staticClass:"col-md-5 col-12"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-43.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-7 col-12"},[_c('h4',[_vm._v("Determinación de los Usos Significativos de la Energía (USE):")]),_c('p',[_vm._v("Normalmente los USE se determinan como las insta