ra para el desempeño energético hacen referencia a las acciones que se pueden implementar con el objetivo de mejorar la eficiencia energética al interior de un proceso, equipo o área de la empresa, y que permite la mejora del desempeño energético del mismo y de la organización. Lo anterior conlleva además en ahorros económicos, disminución de emisiones de gases de efecto invernadero (GEI) y mejora en las condiciones de operación y ambientes de trabajo. Las oportunidades de mejora se deben exponer en una lista ordenada y priorizada dependiendo de las necesidades y políticas de la organización.")])]),_c('div',{staticClass:"col-md-6 col-lg-6 col-12"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-47.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Clasificación"}},[_c('div',{staticClass:"col-md-6 col-lg-6 col-12"},[_c('p',[_vm._v("Generalmente, la priorización y clasificación se puede realizar según los montos de inversión económica de cada acción, iniciando por las que no implican ninguna inversión (como por ejemplo cambio de conducta, prácticas de operación y mantenimiento), luego las que requieren niveles de inversión económica moderado capacitación, implementación de sistemas de monitoreo y automatización por ejemplo, y finalizando con las que requieren inversiones significativas para la organización (renovación de equipos, cambios de aislamiento en grandes longitudes de tuberías, cambio de fuentes de energía, entre otras).")])]),_c('div',{staticClass:"col-md-6 col-lg-6 col-12"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-48.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Priorización"}},[_c('div',{staticClass:"col-md-6 col-lg-6 col-12"},[_c('p',[_vm._v("Si la empresa u organización lo desea, también se pueden priorizar las oportunidades de mejora dependiendo de la cantidad de